@font-face {
  font-family: "sferio-font";
  src: url("../fonts/raleway-regular.woff2") format("woff2"), url("../fonts/raleway-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "sferio-font";
  src: url("../fonts/raleway-semibold.woff2") format("woff2"), url("../fonts/raleway-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}

body {
  background: #ffffff;
  font-family: "sferio-font", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body h2, body .h2 {
  font-size: 35px;
  line-height: 1.1;
  font-weight: normal;
  margin-bottom: 1em;
  color: #45494c;
}

body h3, body .h3 {
  font-size: 24px;
  line-height: 30px;
  font-weight: bold;
  margin-bottom: 1em;
  color: #45494c;
}

body p {
  margin-bottom: 1em;
  font-size: 18px;
  line-height: 27px;
}

body a {
  color: #198adf;
  text-decoration: none;
}

body a:hover {
  text-decoration: underline;
}

body a:visited {
  color: #3a539b;
}

body ol {
  list-style: decimal;
  position: relative;
  left: -5px;
}

@media only screen and (max-width: 720px) {
  body ol {
    margin-left: 2em;
  }
}

body ol li {
  padding-left: 5px;
}

.form__field {
  margin-bottom: 10px;
}

.input, .textarea {
  text-align: center;
  line-height: normal;
  height: 52px;
  background: #eeeeee;
  font-weight: 600;
  color: #393939;
  text-transform: uppercase;
  width: 100%;
}

.input:placeholder, .textarea:placeholder {
  font-size: 14px;
}

.textarea {
  height: 100px;
  padding: 20px;
  border: none;
}

.btn {
  font-size: 14px;
  line-height: 1;
  display: inline-block;
  background: transparent;
  border: 2px solid #ffffff;
  text-decoration: none;
  color: #fff;
  font-weight: 400;
  padding: 10px 30px;
  transition: all 0.3s;
}

.btn:hover {
  color: #ff7c1c;
  border-color: #ff7c1c;
  text-decoration: none;
}

.btn:visited {
  color: #fff;
}

.btn--orange {
  font-weight: 600;
  background: #ff7c1c;
  padding: 15px 40px;
  border-width: 0;
}

.btn--orange:hover {
  background: #ffffff;
  color: #ff7c1c;
  text-decoration: none;
}

.img-responsive {
  max-width: 100%;
}

@media only screen and (min-width: 1200px) {
  .container--full {
    width: 1500px;
  }
}

.icon-logo {
  width: 100px;
  height: 36px;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.f12 {
  font-size: 12px;
}

.bold {
  font-weight: 600;
}

.ml0 {
  margin-left: 0;
}

.sc {
  padding-top: 75px;
  padding-bottom: 75px;
}

.sc--secondary {
  background: #3a539b;
  color: #ffffff;
}

.sc--secondary h2, .sc--secondary p {
  color: #ffffff;
}

.sc--skew {
  transform: skew(0deg, -18deg);
  margin-bottom: 165px;
}

.sc--skew .container {
  transform: skew(0deg, 18deg);
}

.sc__heading {
  text-align: center;
  padding-bottom: 25px;
}

.header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 10;
  background: #3a539b;
  color: #ffffff;
  padding: 15px 0;
}

.header__wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
}

.header__logo {
  margin-right: auto;
}

.footer {
  background: #3a539b;
  padding: 50px 0;
  color: #ffffff;
}

.footer a {
  color: #ffffff;
}

.footer__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__nav {
  padding: 50px 0;
}

.footer__nav .nav__list {
  justify-content: center;
}

.footer__label {
  margin-right: 5px;
  margin-left: 30px;
}

.footer__socials {
  flex: 0 0 100px;
}

.socials__list {
  display: flex;
}

.socials__item {
  margin: 0 10px;
}

.socials__item:first-child {
  margin-left: 0;
}

.socials__item:last-child {
  margin-right: 0;
}

.socials__link {
  font-size: 30px;
}

.nav__list {
  display: flex;
  flex-wrap: wrap;
}

.nav__item {
  margin: 0 15px;
}

.nav__link {
  text-decoration: none;
  color: #ffffff;
}

.nav__link:hover {
  font-weight: 600;
}

.nav__link:visited {
  color: #ffffff;
}

.nav__link:after {
  display: block;
  content: attr(data-title);
  font-weight: bold;
  height: 0;
  overflow: hidden;
  visibility: hidden;
}

.welcome {
  background: #3a539b;
  height: 100vh;
  min-height: 800px;
  margin-top: -14%;
  margin-bottom: 14%;
}

@media only screen and (max-width: 991px) {
  .welcome {
    height: auto;
    min-height: auto;
    padding-top: 100px;
  }
}

.welcome .container {
  padding-top: 14%;
  height: 100%;
}

.welcome .row {
  position: absolute;
  width: 100%;
  bottom: -70px;
}

@media only screen and (max-width: 991px) {
  .welcome .row {
    position: static;
  }
}

.welcome__heading {
  font-size: 40px;
  line-height: 50px;
  font-weight: 600;
  text-align: left;
}

.welcome__img {
  box-shadow: 0px 9px 13px 0px rgba(0, 0, 0, 0.45);
}

.welcome__btn {
  margin-top: 40px;
}

.capability.sc {
  padding-bottom: 15px;
}

.capability__item {
  padding-top: 100px;
}

.capability__add {
  margin-top: 125px;
}

.capability__icon-t {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-top: 50px;
}

.capability__icon-t .icon-t__item {
  flex: 0 0 50%;
}

.capability__icon-t .icon-t__item:nth-child(2n) {
  text-align: right;
}

.icon-t__item {
  margin-bottom: 65px;
}

.icon-t__content {
  display: inline-block;
  text-align: left;
}

.icon-t__i {
  display: inline-block;
  vertical-align: middle;
  width: 70px;
  flex: 0 0 70px;
}

.icon-t__t {
  display: inline-block;
  vertical-align: middle;
  margin-left: 35px;
  max-width: 300px;
}

.icon-t__t p {
  margin-bottom: 0;
}

.scenario.sc {
  padding-top: 0;
  padding-bottom: 0;
  line-height: 0;
}

.scenario__canvas {
  position: relative;
}

.scenario__img {
  width: 100%;
}

.scenario__heading {
  position: absolute;
  top: 75px;
  color: #ffffff;
  width: 100%;
}

.scenario__tip {
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #ffffff;
  box-shadow: 0px 0px 8px 6px #333333;
}

.scenario__tip--1 {
  top: 69%;
  left: 20.3%;
}

.scenario__tip--2 {
  top: 70.4%;
  left: 27.5%;
}

.scenario__tip--3 {
  top: 70.9%;
  left: 44.7%;
}

.scenario__tip--4 {
  top: 69%;
  left: 66%;
}

.scenario__tip--5 {
  top: 63.2%;
  left: 80.3%;
}

.specifications__heading {
  padding-bottom: 75px;
}

.specifications__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.specifications__item {
  flex: 0 0 50%;
  padding-right: 30px;
  margin-bottom: 30px;
}

.specifications__name {
  font-weight: 600;
  margin-bottom: 2px;
}

.connection__item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 50px;
  cursor: pointer;
}

.connection__item--active .connection__num:before {
  background: #3a539b;
}

.connection__item--active .connection__caption {
  color: #3a539b;
}

.connection__tip {
  font-size: 16px;
  margin-top: 75px;
}

.connection__images {
  position: relative;
  min-height: 400px;
}

.connection__image {
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(0, 0);
  transition: transform 0.3s;
  will-change: transform;
}

.connection__image--active {
  transform: scale(1, 1);
}

.num-circle {
  position: relative;
  color: #fff;
  margin: 0 30px 0 20px;
  line-height: 40px;
}

.num-circle:before {
  content: '';
  display: inline-block;
  position: absolute;
  background: #3f4147;
  width: 40px;
  height: 40px;
  z-index: -1;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  border-radius: 50%;
}
