@include font('sferio-font', raleway-regular);
@include font('sferio-font', raleway-semibold, 600);

body {
	background: $color_bg_light;
	font-family: $font;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	h2, .h2 {
		font-size: 35px;
		line-height: 1.1;
		font-weight: normal;
		margin-bottom: 1em;
		color: $color_t; }
	h3, .h3 {
		font-size: 24px;
		line-height: 30px;
		font-weight: bold;
		margin-bottom: 1em;
		color: $color_t; }
	p {
		margin-bottom: 1em;
		font-size: 18px;
		line-height: 27px; }
	a {
		color: $color_link;
		text-decoration: none;
		&:hover {
			text-decoration: underline; }
		&:visited {
			color: $color_bg; } }
	ol {
		list-style: decimal;
		position: relative;
		left: -5px;
		@include r(720) {
			margin-left: 2em; }
		li {
			padding-left: 5px; } } }
.form {
	&__field {
		margin-bottom: 10px; } }
.input,.textarea {
	text-align: center;
	line-height: normal;
	height: 52px;
	background: #eeeeee;
	font-weight: 600;
	color: #393939;
	text-transform: uppercase;
	width: 100%;
	&:placeholder {
		font-size: 14px; } }
.textarea {
	height: 100px;
	padding: 20px;
	border: none; }
.btn {
	font-size: 14px;
	line-height: 1;
	display: inline-block;
	background: transparent;
	border: 2px solid $color_bg_light;
	text-decoration: none;
	color: #fff;
	font-weight: 400;
	padding: 10px 30px;
	transition: all $time;
	&:hover {
		color: $color_orange;
		border-color: $color_orange;
		text-decoration: none; }
	&:visited {
		color: #fff; }
	&--orange {
		font-weight: 600;
		background: $color_orange;
		padding: 15px 40px;
		border-width: 0;
		&:hover {
			background: $color_bg_light;
			color: $color_orange;
			text-decoration: none; } } }

.img-responsive {
	max-width: 100%; }

.container--full {
	@include rmin(1200) {
		width: 1500px; } }

.icon-logo {
	width: 100px;
	height: 36px; }

.text-center {
	text-align: center; }
.text-right {
	text-align: right; }
.f12 {
	font-size: 12px; }
.bold {
	font-weight: 600; }
.ml0 {
	margin-left: 0; }
